const getCurrentLocation = () =>
  new Promise((resolve, reject) => {
    if (!navigator.geolocation) {
      reject(new Error('Taraycnz bu zellii desteklememektedir.'));
      return;
    }

    navigator.geolocation.getCurrentPosition(
      position => {
        resolve({ lat: position.coords.latitude, lon: position.coords.longitude });
      },
      error => {
        let errorMessage = 'Bilinmeyen bir hata olutu.';

        switch (error.code) {
          case error.PERMISSION_DENIED:
            errorMessage = 'Bu zellii kullanabilmek iin izin vermeniz gerekmektedir.';
            break;
          case error.POSITION_UNAVAILABLE:
            errorMessage = 'u an bu zellii kullanamyorsunuz.';
            break;
          case error.TIMEOUT:
            errorMessage = 'Pozisyonunuz saptanrken bir hata meydana geldi.';
            break;
          default:
            errorMessage = 'Bilinmeyen bir hata olutu.';
        }

        reject(new Error(errorMessage));
      },
    );
  });

export default getCurrentLocation;



